import React, { Component } from 'react'
import LOGO from '../../assets/portraits/150px-Icon_Portrait_Feh.png'
import './MergedIVCalculatorApp.css'
import { Switch, Route } from 'react-router-dom'
import asyncComponent from "../AsyncComponent"

const AsyncSearchBar = asyncComponent(() => import("../SearchBar"))
const AsyncHeroPortraits = asyncComponent(() => import("../heroview/HeroPortraits"))
const AsyncHeroStats = asyncComponent(() => import("../heroview/HeroStats"))

class MergedIVCalculatorApp extends Component {

  render () {
    return (
      <div className="App">
        <div className="App-header-container">
          <div className="App-header-middle-container" onClick = {() => window.location = process.env.PUBLIC_URL} >
            <header className="App-header" >
              <img src={LOGO} className="App-logo" alt="logo" />
              <h1 className="App-title">FEH Merged IV Calculator</h1>
            </header>
          </div>
        </div>
        <div className="Search-bar">
          <Route path = '/' component = {AsyncSearchBar} />
        </div>
        <div className="hero-container">
          <Switch>
            <Route exact path = '/' component = {AsyncHeroPortraits} />
            <Route path = '/:heroName' component = {AsyncHeroStats} />
          </Switch>
        </div>
      </div>
    )
  }
}

export default MergedIVCalculatorApp



